
.section{
    background-color: #f1f2f7;
    padding-bottom: 50px;
    
    transition: transform .1s linear;
    will-change: transform;
    z-index: -1;
    
    .header-shadow{
        box-shadow: 0 3px 3px 0 rgba( 1,2,2,0.05);
        background-color: white;
        &.header-shadow.rotate{
            transform: rotateX(var(--rotate));
        }
        &.nostyle{
            box-shadow: none!important;
            background-color: transparent!important;
        }
        
        .convex-heading{
            padding: 15px;
            margin-bottom: 15px;
            color: black;
            position: relative;
            align-items: center;;
            font-weight: 300;
            display: flex;
            justify-content: center!important;
            color: gray;
            .title-content{
                margin-top: 50px;
                font-size: var(--fonts);
                font-weight: 300;
            }
            .border-bottom{
                background-color: #0a6cbd;
                width: 15px;
                height: 15px;
                border-radius: 50%;
                -moz-border-radius: 50%;
                -webkit-border-radius: 50%;
                margin: 0 auto;
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    height: 1px;
                    width: 100px;
                    background: #2791e7;
                    left: -110px;
                    bottom: 5px;
                }
                &::after{
                    content: "";
                    position: absolute;
                    height: 1px;
                    width: 100px;
                    background: #2791e7;
                    right: -110px;
                    bottom: 5px;
                }
            }
        }

        .bg-outside{
          border: solid 1px #f5f5f5;
          padding-top: 5px;
          padding-left: 5px;
          padding-bottom: 3px;
          padding-right: 5px;
          background-color: white;
          border-radius: 50%;
          z-index: 2;
          overflow: hidden;
        }
    }
    .code-frame{
      height: auto;
      margin-bottom: 30px;

      .coding-space{
        --var_color:black;
        --num_color:#597c1d;
        --string_var_color:#5a7a48;
        --control_color: darkblue;
        --manage_color:rgb(114, 7, 114);
        --built_color:#2b2c80;
        font-family: 'Roboto', sans-serif;
        border-top: double 4px black;
        padding-top: 10px;
        width: auto;
        font-weight: 600;
        .code-line{
            margin-bottom: 2px;
            line-height: 25px;
            word-wrap: break-word;
        }
        .child-section{
            padding-left: 40px;
        }
        .code-var{
            color: var(--var_color);
            color: rgb(65, 65, 65);
        }
        .code-numb{
            color: var(--num_color);
        }
        .code-control{
            // color: var(--control_color);
            font-weight: 700;
        }
        .code-manage{
            color: var(--manage_color);
            font-weight: 700;
        }
        .code-built{
            color: var(--built_color);
            font-weight: 700;
        }
        .string-var{
            color: var(--string_var_color);
            font-weight: 700;
        }
        var {
          content:'√';
          display:inline-block;
          vertical-align: top;
          line-height: 1em;
          width: 1em;
          height:1em;
          margin-right: 0.3em;
          text-align: center;
          color: green;
        }
      }
    }
}