
.page-subcol{
  padding-top: 20px;
  padding-bottom: 20px;
  min-height: 225px;
  transition: all 0.3s linear;
  font-family: 'Roboto', sans-serif;
  .subcol-icon{
    font-size: 2em;
    display: inline;
    margin-right: 10px;
    i{
      width: 45px;
      height: 45px;
    }
    img{
      width: 45px;
      height: 45px;
    }
  }
  .subcol-title{
    font-size: 1.4em;
    font-weight: 700;
    text-transform: uppercase;
    display: inline;
  }
  .subcol-content{
    display: flex;
    ul{
      list-style: none;
      padding-left: 60px;
      li{
        color: rgb(68, 68, 68);
        font-weight: 100;
        font-size: 1.2em;
        min-width: 50%;
      }
    }
    span{
      color: rgb(68, 68, 68);
      font-weight: 100;
      font-size: 1.2em;
      padding-left: 60px;
    }
  }
  .subcol-contents{
    p{
      margin-bottom: 10px;
      font-weight: 100;
      font-size:1.1em;
      color: rgb(68, 68, 68);
      padding-left: 10px;
    }
  }
}
.turn-display{
  width: 80px;
  height: 80px;
  border: solid 10px #f1f2f7;
  /* background:#f1f2f7; */
  border-radius: 50%;
  text-align:center;
  top: 100%;
  left: 50%;
  margin-top: -40px;
  margin-left: -40px;
  color: white;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  transition: all 0.3s linear;
  font-weight: 100;
  background-color: rgb(125, 55, 204);
  cursor: pointer;
  p{
    width: 50px;
    height: 50px;
  }
  &:hover{
    transform: rotateZ(360deg) scale(1.3);
    background-color: rgb(155, 41, 41);
  }
}