.hbadge-noflip-stage{
    margin-bottom: 15px;
    .uni-section{
        background-color: white;
        min-height: 200px;
        margin-right: 10px;
        width: 98%;
        border: solid 2px  rgb(236, 236, 236);
        .uni-stage{
            position: absolute;
            &.circle-item{
                width: 120px;
                height: 120px;
                border: solid 10px #f1f2f7;
                border-radius: 50%;
                text-align:center;
                top: 50%;
                left: 100%;
                margin-top: -60px;
                margin-left: -55px;
                color: white;
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                z-index: 2;
                transition: all 0.3s linear;
                p{
                    font-size: 0.9em;
                    font-weight: 100;
                    margin-bottom: 0px;
                }
                &.hover{
                    border-width: 5px;
                    height: 120px;
                    transform: rotateZ(360deg);
                }
                &.item-number-1{
                    background-color: #c0a9ea;
                    &.hover{
                        background-color: #a188cf;
                    }
                }
                &.item-number-2{
                    background-color: #a9c7ea;
                    &.hover{
                        background-color: #88b5cf;
                    }
                }
            }     
        }
        .uni-title{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 100%;
            color: gray;
            .uni-major{
                font-size: 2em;
                font-weight: 100;
                color: black;
            }
            .uni-name{
                display: flex;
                align-items: center;
                flex-flow: column;
                p{
                    margin-bottom: 2px;
                }
            }
        }
    }
    .uni-acvm {
        min-height: 200px;
        display: flex;
        align-items: center;
        background-color: white;
        border: solid 2px  rgb(236, 236, 236);
        z-index: 0;
        .uni-descript{
            ul{
                list-style: none;
                margin-bottom: 0px;
                font-weight: 100;
            }
            li{
                word-wrap: break-word;
                display: inline-block;
                margin-bottom: 1px;
                margin-top: 5px;
                margin-bottom: 5px;
                &:before{
                    content: '►';
                    padding-right: 5px;
                    font-size: 1.2em;
                }
            }
        }
    }
}
@media (max-width:768px){
    .hbadge-noflip-stage{
        .uni-section{
            .uni-stage{
                &.circle-item{
                    width: 100px!important;
                    height: 100px!important;
                    top: 105%!important;
                    left: 55%!important;
                }
            }
        }
    }
}
