
.hbadge-des-section{
    padding: 40px;
    display: flex;
    justify-content: center;
}
.hbadge-stage{
    margin-top: 20px;
    .col-timeline{
        border-right: double 4px gray;
    }
    .hbadge-info.flx-reverse{
        flex-direction: row-reverse;
    }
    // .hbadge-section.flx-reverse {
    // }
    .hbadge-section{
        background: white;
        border: solid 2px rgb(236, 236, 236);
        border-right: solid 20px #f1f2f7;
        .hbadge-e-stage {
            position: absolute;
            &.hbadge-circle-item{
                width: 160px;
                height: 160px;
                border: solid 20px #f1f2f7;
                border-radius: 50%;
                text-align:center;
                top: 50%;
                left: 100%;
                margin-top: -80px;
                margin-left: -70px;
                color: white;
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                z-index: 2;
                background-color: #c0a9ea;
                transition: all 0.3s linear;
                p{
                    font-size: 0.9em;
                    font-weight: 100;
                    margin-bottom: 0px;
                }
                &.hover{
                    background-color: #a188cf;
                    border-width: 5px;
                    font-size: 1.3em;
                    transform: rotateZ(360deg);
                }
            }
            &.hbadge-circle-item.item-1{
                background-color: rgb(47, 158, 134);
            }
            &.hbadge-circle-item.item-1.hover{
                background-color: rgb(36, 121, 102);
            }
            &.hbadge-circle-item.item-2{
                background-color: rgb(194, 52, 52);
            }
            &.hbadge-circle-item.item-2.hover{
                background-color: rgb(129, 35, 35);
            }
        }
        &.flx-reverse{
            border: solid 2px rgb(236, 236, 236);
            border-left: solid 20px #f1f2f7;
            .hbadge-e-stage{
                left: 0%;
                margin-top: -80px;
                margin-left: -90px;
            }
        }
        .hbadge-title{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 100%;
            color: gray;
            .hbadge-position{
                font-size: 1.5em;
                font-weight: 400;
                color: black;
            }
        }
    }
    .hbadge-acvm{
        border: solid 2px rgb(236, 236, 236);
        padding-left: 5%;
        background-color: white;
        ul{
            list-style: '• ';
            // list-style-type: none;
            padding: 15px 20px;
            b{
                font-weight: 500;
                width: fit-content;
                display: inline-block;
            }
            li{
                font-weight: 100;
                line-height: 30px;
                div{
                    margin-left: 10px;
                    display: block;
                }
                p{
                    font-weight: 500;
                    font-size: 1.1em;
                    color: #0a6cbd;
                    display: block;
                }
                span{
                    margin-left: 20px;
                    display: block;
                    &:before{
                        content: '- ';
                        padding-right: 5px;
                        font-size: 1.2em;
                    }
                }
                a{
                    color: unset;
                    border-bottom: solid 1px black;
                    &:hover{
                        border-bottom: solid 2px black;
                    }
                }
                .status{
                    display: inline-block;
                    font-weight: 900;
                    font-size: 0.75em;
                    padding: 0px 10px;
                    position: absolute;
                    border-radius: 5px;
                    margin-left: 0px;
                    &:before{
                        content: ''!important;
                    }
                    &.done{
                        color: rgb(59, 185, 59);
                    }
                    &.inprogess{
                        color: rgb(47, 158, 134);
                    }
                }
                
            }
        }
    }
}

@media (max-width:768px){
    
    .hbadge-des-section{
        padding: 40px;
        display: flex;
        justify-content: center;
    }
    .hbadge-title{
        align-items:unset!important;
        .hbadge-position{
            max-width: 80%;
        }
    }
    .flx-reverse .hbadge-title{
        align-items: flex-end!important;
        .hbadge-position{
            max-width: 80%;
            text-align: end;
        }
    }
    .hbadge-section{
        &.flx-reverse.hbadge-e-stage.hbadge-circle-item{
                width: 120px!important;
                height: 120px!important;
                right: 10!important;
                border: solid 10px #f1f2f7!important;
                margin-top: -60px!important;
                margin-left: -50px!important;
        }
        
        .hbadge-e-stage.hbadge-circle-item{
                width: 120px!important;
                height: 120px!important;
                right: 10!important;
                border: solid 10px #f1f2f7!important;
                margin-top: -60px!important;
                margin-left: -70px!important;
        }
    }
    .hbadge-acvm{
        ul{
            li{
                .status{
                    padding: 0px!important;
                    margin-left: -2px!important;
                }
            }
        }
    }
}
