.bkg-banner{
    width: 100%;
    height: 100vh;
    background-position:50% 0px;
    opacity: 0.8;
    position: relative;
    background-repeat:repeat-y ;
    transition: background-position 250ms linear;
    background-size: cover;
    .bkg-layer{
        background-color: black;
        width: 100%;
        height: 100%;
        z-index: 1;
        position: absolute;
        opacity: 0.5;
    }
    .color-bkg{
        /* background-color: rgba(26, 188, 156, 0.8); */
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 2;
    }
    .title {
        margin-top: 25vh;
        overflow: hidden;
        height: auto;
        color: white;
        font-size: 5.5em;
        border-bottom: 4px double #FFF;
        font-weight: 600;
        .display-2s{
            color: white!important;
            font-size: 5.5rem;
            font-weight: 100;
            line-height: 1.2;
            font-family: 'Montserrat';
            font-weight: 100;
            margin-bottom: 0;
        }
    }
    .par {
      overflow: hidden;
      align-items: center;
      padding: 0 auto ;
      color: white;
      font-size: 1.2em;
      .quick-des{
        color: white;
        height: 60px;
        display: inline-block;
        .sub-quickdes{
          font-size: 1.2em;
          font-weight: 500;
          &:after{
            content: "|";
            font-size: 1.5em;
            font-weight: 200;
          }
        }
      }
    }
    .contact-sections{
        margin-right: 20%;
        margin-left: 20%;
        .a-tag-contact{
            position: relative;
            display: block;
            width: 50px;
            height: 50px;
            color: white;
            border: 1px solid #FFF;
            border-radius: 50%;
            text-align: center;
            padding-top: 4px;
            margin: 3px;
            z-index: 0;
            cursor: pointer;
            transition: all 200ms linear;
            .navbar-nav-svg{
                margin-top: 15%;
                font-size: 1.5em;
            }
        }
        .a-tag-contact:hover{
            background-color: white;
            color: black;
        }
    }
    #gmail-alert{ 
      overflow: hidden;
      text-overflow: ellipsis;
      width: 370px;
      height: auto;
      white-space: nowrap;
      padding: 0.75rem 1.5rem;
      display: none;
      animation: showing 0.3s linear;
      position: fixed;
      top: 50px;
      left: 10px;
      z-index: 1;
    }
    @keyframes showing{
      from{
        width: 0;
        height: 0;
        padding: 0;
      }
      to{
        width: 370px;
        height: 50px;
        padding: 0.75rem 1.5rem;
      }
    }
}