
.page-content{
    padding: 0px 20px;
    font-family: 'Roboto', sans-serif;
    .cover-img{
      display: flex;
      justify-content: center;
      .container_img {
        height: 300px;
        width: 300px;
        max-width: 100%;
        border-radius: 15%;
        box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
      }
    }
    .quickview-info{
      margin-top: 50px;
      padding-left: 70px;
      padding-right: 70px;
      .birthday{
        display: inline;i{
            font-size: 1.5em;
            color: brown;
            width: 35px;
          }
      }
      .my-cv{
        color: brown;
        padding: 5px;
        font-style: oblique;
        border-radius: 5px;
        cursor: pointer;
        display: inline;
        font-weight: 700;
        i{
            width: 25px;
        }
        a{
            color: inherit;
        }
      }
    }
    .contact-area{
        margin-left: 10px;
        .contact-show{
          height: 200px;
          display: inline-block;
          .contact-title{
            font-size: 2em;
            font-weight: 100;
          }.contact-list{
            list-style: none;
            padding-left: 10px;
          }
          .contact-list{ 
            li, a{
              line-height: 40px;
              display: none;
              font-style: normal;
              color: inherit;
            }
          }
          .contact-list{
            li, a 
            {
              i{
                font-size: 1.5em;
                color: brown;
                width: 35px;
              }
            }
          } 
          .contact-list{
              li, a{
                span{ 
                  font-size: 1.2em;
                  font-weight: 300;
                  font-style: italic;
              }
            } 
          } 
        }
        .code-frame{
          height: auto;
          margin-bottom: 30px;
        }
    }
    
    .page-content-col{
        padding: 20px;
        border-radius: 3px;
        background-color: white;
        border: solid 2px #fefefe;
        .page-col-title{
          font-size: 1.5em;
          font-weight: 100;
          .page-col-content{
            font-size: 1em;
            font-weight: 100;
          }
        }
        .emphasize{
          font-weight: 500;
        }
    }
}
.page-content-col{
padding: 20px;
border-radius: 3px;
background-color: white;
border: solid 2px #fefefe;
}
  