
.page-content{
    padding: 0px 20px;
    font-family: 'Roboto', sans-serif;
    
    .contact-area{
        margin-left: 10px;
        .contact-show{
          height: 200px;
          .contact-title{
            font-size: 2em;
            font-weight: 100;
          }.contact-list{
            list-style: none;
            padding-left: 10px;
          }
          .contact-list li{
            line-height: 40px;
            display: none;
          }
          .contact-list li i{
            font-size: 1.5em;
            color: brown;
            width: 35px;
          
          }
          .contact-list li span{
            font-size: 1.2em;
            font-weight: 300;
            font-style: italic;
          }
        }
        .code-frame{
          height: auto;
          margin-bottom: 30px;
        }
    }
    
    .page-content-col{
        padding: 20px;
        border-radius: 3px;
        background-color: white;
        border: solid 2px #fefefe;
        .page-col-title{
          font-size: 1.5em;
          font-weight: 100;
          .page-col-content{
            font-size: 1em;
            font-weight: 100;
          }
        }
        .emphasize{
          font-weight: 500;
        }
    }
}
.page-content-col{
padding: 20px;
border-radius: 3px;
background-color: white;
border: solid 2px #fefefe;
